        <div class="container mb-4">
            <h2 class="text-secondary text-center mb-3">О Нас</h2>
            <div class="row align-items-center">
                <div class="col-md-6 col-sm-12">
                    <img
                        class="my-2 img-fluid"
                        src=".././assets/images/about/about.jpg"
                        alt="About us"
                    />
                </div>
                <div class="col-md-6 col-sm-12">
                    <p class="mt-2 text-secondary lead">
                        Lorem Ipsum is simply dummy text of the printing and
                        typesetting industry. Lorem Ipsum has been the
                        industry's standard dummy text ever since the 1500s,
                        when an unknown printer took a galley of type and
                        scrambled it to make a type specimen book. It has
                        survived not only five centuries, but also the leap into
                        electronic typesetting, remaining essentially unchanged.
                        It was popularised in the 1960s with the release of
                        Letraset sheets containing Lorem Ipsum passages, and
                        more recently with desktop publishing software like
                        Aldus PageMaker including versions of Lorem Ipsum.
                    </p>
                </div>
            </div>
        </div>
        <div class="about-parallax mt-5" v-view.once="animateAbout">
            <div class="container row mx-auto py-5">
                <div 
                    class="col-md-3 col-sm-6 text-center my-2 parallaxItem animate__animated animate__slow"
                    v-for="(item, idx) in aboutData"
                    :key="idx"
                >
                    <img
