    </section>
</template>

<script>
export default {
    name: "About",
    data() {
        return {
            aboutData: [
                {imageSrc: require('.././assets/images/about/quality.png'), text: 'Гарантия Качества'},
                {imageSrc: require('.././assets/images/about/fast.png'), text: 'Кротчайшие Сроки'},
                {imageSrc: require('.././assets/images/about/best-price.png'), text: 'Лучшая Цена'},
                {imageSrc: require('.././assets/images/about/best-worker.png'), text: 'Квалифицированые работники'},
            ]
        }
    },
    methods: {
        animateAbout(e) {
            const aboutImgs = document.querySelectorAll('.parallaxItem');
            aboutImgs.forEach(el => {
                el.classList.add('animate__fadeInDown');
                el.style.opacity = '1';
            });
        }
    },
};
</script>

<style lang="scss">
.about-parallax {
    background-image: url(".././assets/images/about/about3.jpg");
    background-attachment: fixed;
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    position: relative;
    @media (max-width: 767px) {
        background-position: 25%;
    }
    .container {
        position: relative;
        z-index: 2;
    }
    &::after {
        position: absolute;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        background-color: rgba(0, 0, 0, 0.65);
        z-index: 1;
        content: "";
    }
    .img-about {
        filter: invert(1);
        margin-bottom: 1.5rem;
    }
    .parallaxItem {
        opacity: 0;
    }
}
</style>