<template>
    <div id="gallery" class="gallery container py-3" v-view.once="animateGallery">
        <h2 class="text-secondary text-center mb-3">Галерея</h2>
        <div class="row">
            <div
                class="col-md-6 col-sm-12 my-2"
                v-for="(image, i) in images.slice(0, 4)"
                :key="i"
            >
                <img
                    class="gallery__img image img-fluid animate__animated animate__slow"
                    :src="image"
                    @click="index = i"
                    ref="galleryImg"
                />
            </div>
        </div>
        <vue-gallery-slideshow
            :images="images"
            :index="index"
            @close="index = null"
        ></vue-gallery-slideshow>
    </div>
</template>

<script>
import VueGallerySlideshow from "vue-gallery-slideshow";

export default {
    components: {
        VueGallerySlideshow,
    },
    data() {
        return {
            images: [
                require("../assets/images/gallery/g1.jpg"),
                require("../assets/images/gallery/g2.jpg"),
                require("../assets/images/gallery/g3.jpg"),
                require("../assets/images/gallery/g4.jpg"),
                require("../assets/images/gallery/g5.jpg"),
                require("../assets/images/gallery/g1.jpg"),
                require("../assets/images/gallery/g2.jpg"),
                require("../assets/images/gallery/g3.jpg"),
                require("../assets/images/gallery/g4.jpg"),
                require("../assets/images/gallery/g5.jpg"),
            ],
            index: null,
        };
    },
    methods: {
        animateGallery() {
            this.$refs.galleryImg.forEach(img => {
                img.classList.add('animate__rollIn')
            })
        }
    }
};
</script>

<style lang="scss">
.gallery {
    &__img {
        cursor: zoom-in;
    }
}
</style>