<template>
    <header class="header" id="home" v-view="headerAnimation">
        <Navbar />
        <div class="header__main py-5 animate__animated animate__backInDown animate__slow mx-auto px-3">
            <h1 class="text-primary text-inline_auto">In-Line Auto</h1>
            <p class="text-secondary header-text">
                Lorem ipsum dolor sit amet consectetur adipisicing elit.
                Temporibus debitis pariatur
            </p>
        </div>
        <div class="headerBg" ref="headerBg"></div>
    </header>
</template>

<script>
import Navbar from "./Navbar";

export default {
    name: "Header",
    components: {
        Navbar,
    },
    methods: {
        headerAnimation(e) {
            const headerBg = this.$refs.headerBg;

            let headerBgTransformVal = 1 + Math.abs(headerBg.getBoundingClientRect().top) / 1200;

            headerBg.style.transform = `scale(${headerBgTransformVal})`;
        }
    },
};
</script>

<style lang="scss">
.header {
    overflow: hidden;
    &-text {
        font-size: 2.75rem;
        @media(max-width: 450px) {
            font-size: 1.75rem;
        }
    }
    &__main {
        position: absolute;
        top: 0;
        left: 0;
        bottom: 0;
        right: 0;
        height: 75vh;
        max-width: 800px;
        z-index: 1;
        display: flex;
        flex-direction: column;
        justify-content: center;
        text-align: center;
        align-items: center;
    }
    .headerBg {
        height: 75vh;
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        background-image: url(".././assets/images/header/slide3.jpg");
        background-size: cover;
        background-position: center;
        background-repeat: no-repeat;
    }
}
</style>