<template>
    <section class="contact py-3" id="contacts">
        <div class="container">
            <h2 class="text-secondary text-center mb-3">Контакты</h2>

            <div class="my-2">
                <iframe
                    src="https://www.google.com/maps/embed?pb=!1m18!1m12!1m3!1d2721.3951033988374!2d28.86372231576427!3d46.9932145381405!2m3!1f0!2f0!3f0!3m2!1i1024!2i768!4f13.1!3m3!1m2!1s0x40c979537107ac5b%3A0xda8e4ed458d4037!2zU3RyYWRhIE5pY29sYWUgWmVsaW5za2kgNSwgQ2hpyJlpbsSDdSwg0JzQvtC70LTQsNCy0LjRjw!5e0!3m2!1sru!2s!4v1608727114209!5m2!1sru!2s"
                    width="100%"
                    height="450"
                    frameborder="0"
                    style="border: 0"
                    allowfullscreen=""
                    aria-hidden="false"
                    tabindex="0"
                ></iframe>
            </div>

            <div class="row justify-content-space-between mt-3" v-view.once="animateContact">
                <div
                    class="col-md-4 text-center"
                    v-for="(item, idx) in contactData"
                    :key="idx"
                >
                    <div v-if="!item.hasLink">
                        <img
                            class="img-fluid contact__img mb-2"
                            :src="item.img"
                            width="50"
                            height="50"
                            alt=""
                        />
                        <p class="text-secondary contact__text">
                            {{ item.text }}
                        </p>
                    </div>

                    <div v-else>
                        <a :href="item.linkData">
                            <img
                                class="img-fluid contact__img mb-2 animate__animated animate__slow animate__infinite infinite"
                                :src="item.img"
                                width="50"
                                height="50"
                                alt=""
                                ref="contactItem"
                            />
                        </a>
                        <p class="text-secondary contact__text">
                            {{ item.text }}
                            <a class="text-secondary" :href="item.linkData">
                                {{ item.linkText }}
                            </a>
                        </p>
                    </div>
                </div>
            </div>
        </div>
    </section>
</template>

<script>
export default {
    data() {
        return {
            contactData: [
                {
                    text: "Адрес: Зелинского 5/5",
                    hasLink: false,
                    img: require("../assets/images/contacts/location.png"),
                },
                {
                    text: "Контакты: ",
                    hasLink: true,
                    linkData: "tel:+373 69 555 759",
                    linkText: "+373 69 555 759",
                    img: require("../assets/images/contacts/phone.png"),
                },
                {
                    text: "E-mail: ",
                    hasLink: true,
                    linkData: "mailto:inlineauto@gmail.com",
                    linkText: "inlineauto@gmail.com",
                    img: require("../assets/images/contacts/mail.png"),
                },
            ],
        };
    },
    methods: {
        animateContact() {
            this.$refs.contactItem.forEach((item, idx) => {
                const contactAnimations = ['animate__swing', 'animate__tada']
                item.classList.add(contactAnimations[idx]);
            });
        }
    }
};
</script>

<style lang="scss">
.contact__img {
    filter: invert(1);
}
.contact__text {
    font-size: 1.25rem;
}
</style>