<template>
    <section class="services py-3" id="services" v-view.once="animateServices">
        <div class="container mb-4">
            <h2 class="text-secondary text-center mb-3">Услуги</h2>
            <div class="row justify-content-center">
                <div 
                    class="col-lg-4 col-md-6 col-xs-12 my-2 text-center"
                    v-for="(item, idx) in servicesData"
                    :key="idx"
                >
                    <h3 class="text-secondary">
                        {{ item.text }}
                    </h3>
                    <div class="services__subscription">
                        <img
                            class="img-services img-fluid animate__animated"
                            :src="item.imageSrc"
                            :alt="item.text"
                        />
                        <div class="services__subscription-text">
                            <h3 class="text-secondary">
                                {{ item.subText }}
                            </h3>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </section>
</template>

<script>
export default {
    name: "Services",
    data() {
        return {
            servicesData: [
                {imageSrc: require('../assets/images/services/services1.jpg'), text: 'Название Услуги', subText: 'Тут будет описание услуги'},
                {imageSrc: require('../assets/images/services/services2.jpg'), text: 'Название Услуги', subText: 'Тут будет описание услуги'},
                {imageSrc: require('../assets/images/services/services3.jpg'), text: 'Название Услуги', subText: 'Тут будет описание услуги'},
                {imageSrc: require('../assets/images/services/services1.jpg'), text: 'Название Услуги', subText: 'Тут будет описание услуги'},
                {imageSrc: require('../assets/images/services/services2.jpg'), text: 'Название Услуги', subText: 'Тут будет описание услуги'},
                {imageSrc: require('../assets/images/services/services3.jpg'), text: 'Название Услуги', subText: 'Тут будет описание услуги'},
            ]
        }
    },
    methods: {
        animateServices(e) {
            const servicesImgs = document.querySelectorAll('.img-services');
            const animationsArr = ['animate__fadeInLeft', 'animate__fadeInUp', 'animate__fadeInRight', 'animate__fadeInLeft', 'animate__fadeInUp', 'animate__fadeInRight'];
            servicesImgs.forEach((img, idx) => {
                img.classList.add(animationsArr[idx]);
            });
        }
    }
};
</script>

<style lang="scss">
.services {
    &__subscription {
        position: relative;
        &-text {
            position: absolute;
            top: 0;
            right: 0;
            bottom: 0;
            left: 0;
            display: flex;
            flex-direction: column;
            align-items: center;
            justify-content: center;
            background-color: rgba(0, 0, 0, .75);
            opacity: 0;
            transition: .3s all ease-in-out;
            &:hover {
                opacity: 1;
            }
        }
    }
}
</style>